import { Parise } from '@/api/index'
export default {
  name: 'clock', // 
  data() {
    return {
      // 
    }
  },
  props: {
    // 
    clocklist:{
      type:Array
    },
    name:{
      type:String,
      default:sessionStorage.getItem('userName')
    },
  },
  methods: {
    //  
    fullClick(i){
      let obj=this.clocklist[i];
      if(obj.fullTxt==''){
        obj.isFull=false;
        obj.fullTxt='';
      }else{
        obj.isFull=true;
        obj.fullTxt='';
      }
      this.$set(this.clocklist,i,obj);
    },
    isPariseFn(i){
      if(this.name==''){
        return false
      }
      let id=this.clocklist[i].pkid;
      let that=this;
      Parise({logId:id}).then(res =>{
        let obj=that.clocklist[i];
        obj.isParise=true;
        if(res.msg==''){
          if(that.strInArr(obj.praise)){
            obj.praise.push(that.name);
          }
          that.$set(that.clocklist,i,obj);
        }else{
          alert('')
          obj.isParise=false;
        }
    })
    },
    strInArr(arr){
      if(arr.length==0){
        return true
      }
      for(let x of arr){
        if(x==this.name){
          return false
        }else{
          return true
        }
      }
    },
  }

}




